import { Button, Container } from "@material-ui/core";
import {
  Grid,
  Card,
  CardActionArea,
  CardMedia,
  Typography,
  CardContent,
  makeStyles,
} from "@material-ui/core";
import React from "react";
import { useHistory } from "react-router";

const useStyles = makeStyles((theme) => ({
  card: {
    maxWidth: "100%",
    marginRight:"15px",
    marginLeft:"15px",
    minWidth:300,
    borderRadius:'15px',
    
    [theme.breakpoints.down("sm")]: {
      marginRight:"auto",
    marginLeft:"auto",
    marginBottom:"15px",
     },
  },
  media: {
    height: 240,
  },
  flex: {
    display: "flex",
    marginTop: "10vh",
    marginBottom: "10vh",
    marginLeft: "auto",
    marginRight: "auto",
    [theme.breakpoints.down("sm")]: {
     flexWrap: 'wrap',
    // width: '95%',
    },
    // flexWrap: 'wrap',
    // width: '95%',
  },
  btn:{
    [theme.breakpoints.down("sm")]: {
      width:"30vw",
    display:"block",
    marginLeft:"auto",
    marginRight:"auto"
     },
    
  }
}));

const Discussions = () => {
  const classes = useStyles();
  const history = useHistory();

  return (
    <Container maxWidth="lg" className={classes.blogsContainer}>
      <Grid container spacing={3}>
        <Grid item className={classes.flex}>
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                className={classes.media}
                //   image="https://images.pexels.com/photos/2004161/pexels-photo-2004161.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
                image="https://picsum.photos/seed/picsum/750/1260"
                title="Contemplative Reptile"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  Acting
                </Typography>
               <Button variant="contained" className={classes.btn} color="secondary" onClick={() => history.push("/discussions/chat") }>
                   Join Group
               </Button>
              </CardContent>
            </CardActionArea>
          </Card>
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                className={classes.media}
                //   image="https://images.pexels.com/photos/2004161/pexels-photo-2004161.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
                image="https://picsum.photos/seed/picsum/750/1260"
                title="Contemplative Reptile"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  Art
                </Typography>
               <Button variant="contained" color="secondary"  className={classes.btn} onClick={() => history.push("/discussions/chat") }>
                   Join Group
               </Button>
              </CardContent>
            </CardActionArea>
          </Card>
          <Card className={classes.card}>
            <CardActionArea>
              <CardMedia
                className={classes.media}
                //   image="https://images.pexels.com/photos/2004161/pexels-photo-2004161.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
                image="https://picsum.photos/seed/picsum/750/1260"
                title="Contemplative Reptile"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  Music
                </Typography>
               <Button variant="contained" color="secondary" className={classes.btn} onClick={() => history.push("/discussions/chat")}>
                   Join Group
               </Button>
              </CardContent>
            </CardActionArea>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Discussions;
