import {
  Box,
  Button,
  Grid,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import React, { useState } from "react";
import { makeStyles } from "@material-ui/core";
import home1 from "../home1.jpg";
import home2 from "../home2.jpg";
import MobileView from "./MobileView";

const useStyles = makeStyles({
  background: {
    marginTop: "5px",
    backgroundColor: "white",
    width: "90vw",
    marginRight: "auto",
    marginLeft: "auto",
    borderRadius: "10px",
  },
  twodiv1: {
    display: "grid",
    gridTemplateColumns: "4fr 2fr",
    padding: "30px",
  },
  twodiv2: {
    display: "grid",
    gridTemplateColumns: "2fr 4fr",
    padding: "30px",
  },
  mobileview: {
    display: "grid",
    // gridTemplateRows: "1fr 1fr",
  },
  img: {
    width: "100%",
    height: "auto",
  },
  padding: {
    padding: "15px",
  },
  text: {
    justifyContent: "center",
    alignItems: "center",
    padding: "30px",
    // fontSize:'0.8rem'
  },
  button: {
    backgroundColor: "black",
    color: "white",
    padding: "10px",
    marginTop: "15px",
    width: "8rem",
    display: "block",
    marginRight: "auto",
  },
  topic: {
    // fontSize: "2.5rem",
    paddingBottom: "10px",
  },
  landing: {
    position: "relative",
  },
  landing_img: {
    width: "100%",
    height: "90vh",
  },

  text_: {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
  },
  text_center: {
    textAlign: "center",
    paddingBottom: "20px",
    color: "#EEA47FFF",
  },
  button_main: {
    marginLeft: "40%",
    //  marginRight:"auto"
  },
  show_more: {
    display: "none",
  },
});

